<template>
  <div id="app">
    <div id="enter-content" v-if="!siteEntered">
      <h3 class="name">Blair Muraoka Website {{ test }}</h3>
      <div>email: blairmuraoka@gmail.com</div>
      <button @click="enterSite()">Enter</button>
    </div>
    <transition name="real-site">
      <div id="real-site" v-if="siteEntered">
        <transition name="outer">
          <div id="outer" v-if="siteEnteredCompleted" :class="{'resume-selected': activeTab == 3}">
            <div id="inner">
              <div id="mememe" :class="{'name-clicked': nameClicked, 'resume-open': activeTab == 3 }"  @click="nameClickHandler()">
                <div class="me-inner">
                  <div class="front">
                    <img src="../public/images/blairlazyeye.png" alt="" class="headshot">
                  </div>
                  <div class="back">
                    <div class="back-text">uwu</div>
                  </div>
                </div>
              </div>

              <CenterContent v-bind:activeTab="activeTab"/>
              
              <div id="nav">
                <div class="nav-item" :class="{selected: activeTab == 1 }" @click="setActiveTab(1)">
                  About
                </div>
                <div class="nav-item" :class="{selected: activeTab == 2 }" @click="setActiveTab(2)">
                  Contact
                </div>
                <div class="nav-item" :class="{selected: activeTab == 3 }" @click="setActiveTab(3)">
                  Resume
                </div>
                <div class="nav-item links">
                  <a target="_blank" href="https://github.com/blairmuraoka"><i class="fab fa-github-alt"></i></a>
                  <a target="_blank" href="https://www.linkedin.com/in/blair-muraoka-088746146/"><i class="fab fa-linkedin-in"></i></a>
                  <a target="_blank" href="https://www.instagram.com/blaire_yasuo/"><i class="fab fa-instagram"></i></a>
                  <a target="_blank" href="https://www.facebook.com/blairmuraoka"><i class="fab fa-facebook-f"></i></a>
                </div>
              </div>
            </div>
          </div>
        </transition>
      </div>
    </transition>
  </div>
</template>

<script>
import CenterContent from './components/CenterContent.vue';


export default {
  name: 'App',
  components: {
    CenterContent
  },
  data: () => {
    return {
      name: 'Blair Muraoka',
      siteEntered: false,
      test: '',
      activeTab: 1,
      nameClicked: false,
      senderEmail: '',
      emailBody: '',
      siteEnteredCompleted: false // second transition
    }
  },
  methods: {
    enterSite() {
      this.siteEntered = true;
      this.sleep(100).then(() => this.siteEnteredCompleted = true);
    },
    setActiveTab(index) {
      if (index != this.activeTab) {
        this.activeTab = null;
        this.sleep(100).then(() => this.activeTab = index);
      }
    },
    nameClickHandler() {
      this.nameClicked = !this.nameClicked;
    },
    sleep(ms) {
      return new Promise(resolve => setTimeout(resolve, ms));
    }
  }
}
</script>

